<style scoped>
/* 工会 */
main {
  display: flex;
  flex-direction: column;
  overflow: auto;
  flex: 1;
}
.labor {
  width: 100%;
  height: 7rem;
  background-color: #e2e2e2;
}
.labor .union {
  width: 95%;
  height: 6rem;
  background-color: white;
  margin: 0.5rem 2.5% 0.5rem 2.5%;
  border-radius: 0.4rem;
}
.labor .union .union1 {
  width: 96%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e7e7e7;
}
.labor .union .union1 p {
  font-size: 0.9rem;
  color: #000000;
}
.labor .union .union2 {
  width: 96%;
  height: 3.9rem;
}
.labor .union .union2 .line {
  width: 100%;
  height: 1.9rem;
}
.labor .union .union2 .line p {
  font-size: 0.72rem;
  color: #a2a2a2;
  margin-left: 2%;
}
</style>


